@import "./variable";

.a11y-hidden, legend{
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  clip-path: polygon(0 0,0 0,0 0);
}
ul, ol, li{
  list-style-type: none;
  padding-left: 0;
}
fieldset{
  border: 0;
  margin: 0;
  padding: 0;
}
img{
  vertical-align: top;
}
a{
  color: inherit;
  text-decoration: none;
}
html{
  font-size: 10px;
}
body{
  font-family: 'Spoqa Han Sans', 'Sans-serif';
  font-size: 1.4rem;
  font-weight: 400;
  color: $primary-color;
  background-color:  $primary-bg-color;
}
body *, body *::before, body *::after{
  box-sizing: border-box;
}
